@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@font-face {
  font-family: 'sephora';
  src: url('./Sephora.ttf');
}
body {
  background: #0c0c0c;
  background: linear-gradient(45deg,#0000 49%,#040404 49% 51%,#0000 51%),linear-gradient(-45deg,#0000 49%,#040404 49% 51%,#0000 51%);
  background-color: #000;
  background-size: 4em 4em;
  color: #fff;
  font-family: 'poppins';
  height: 100vh;
  opacity: 1;
  width: 100vw;
  margin: 0;
  overflow: hidden;
}
a{
  color: inherit;
  text-decoration: inherit;
}
.glowAura{
  background: linear-gradient(#535353, transparent);
  filter: blur(3rem);
  height: 10vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}
.designerText{
  text-align: center;
  width: 90%;
  font-family: Sephora;
  /* background: linear-gradient(45deg, rgb(236, 239, 241), rgb(69, 91, 105)) text; */
  /* background: linear-gradient(45deg, rgb(236, 239, 241), rgb(255, 255, 255)) text; */
  color: white;
}

.btnAlpha {
  align-items: center;
  background: linear-gradient(145deg, #242424, #323e42);
  background: linear-gradient(145deg, #1c2428, #3a4b52, #161c1f);
  border-radius: 20px;
  border-radius:5px;
  margin: 4vh 0;
  box-shadow: 20px 20px 60px #0f0f0f, -20px -20px 60px #151515;
  box-shadow: 0 5px 3rem #131212;
  color: #ffffff9e;
  color: #fff;
  /* cursor: pointer; */
  display: flex;
  font-size: 9px;
  font-size: 8px;
  justify-content: space-around;
  margin-right: 10px;
  padding: 0 25px;
  position: relative;
  transition: .1s ease-in;
  width: fit-content;
}

.gratitudeBox{
  display: flex;
  width: 100vw;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

@media (max-width:720px){
  .gratitudeBox{
    overflow-y: scroll;
    justify-content: flex-start;
  }


}