body {
  background: #0c0c0c;
  background: linear-gradient(45deg,#0000 49%,#040404 49% 51%,#0000 51%),linear-gradient(-45deg,#0000 49%,#040404 49% 51%,#0000 51%);
  background-color: #000;
  background-size: 4em 4em;
  color: #fff;
  font-family: Montserrat;
  height: 100vh;
  opacity: 1;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
